<template>
  <div class="container">
    <div class="stepBox">
      <span v-for="tab in currentSetTab">
        <!-- <i class="fas fa-check-circle"></i> -->
        <span v-if="currentTab === tab" class="active">
          <i class="fas fa-dot-circle"></i>
          <span class="tabTxt">{{tab}}</span>
        </span>
        <span v-else>
          <i class="far fa-circle" ></i>
          <span class="tabTxt">{{tab}}</span>
        </span>
      </span>
    </div>
    <div class="step1" v-if="currentTab == 'step1'">
      <div class="titleBox">
        <div class="mainTitle">Create Account</div>
        <div class="subTitle">Glad to see you here!</div>
      </div>
      <div class="formBox">
        <form @submit.prevent="validateStep1">
          <div class="form-group" :class="{'error' : attemptSubmit && missingAccount}">
            <label class="formLabel">Account<span class="required">*</span></label>
            <input type="email" class="formControl" placeholder="example@email.com" v-model="account">
            <div class="formFeedback" v-if="attemptSubmit && missingAccount">This field is required.</div>
          </div>
          <div class="form-group" :class="{'error' : attemptSubmit && missingPassword}">
            <label class="formLabel">Password<span class="required">*</span></label>
            <input type="password" class="formControl" placeholder="Password" v-model="password">
            <div class="formFeedback" v-if="attemptSubmit && missingPassword">This field is required.</div>
          </div>
          <div class="form-group" :class="{'error' : attemptSubmit && missingCheckPassword || errorCheckPassword}">
            <label class="formLabel">Comfirm Password<span class="required">*</span></label>
            <input type="password" class="formControl" placeholder="Comfirm Password" v-model="checkPassword">
            <div class="formFeedback" v-if="attemptSubmit && missingCheckPassword">This field is required.</div>
            <div class="formFeedback" v-if="attemptSubmit && errorCheckPassword">Password not match.</div>
          </div>
          <button class="formButton" @click="nextStep2">SUBMIT & NEXT</button>
        </form>
      </div>
    </div>
    <!-- step1 -->
    <div class="step2" v-if="currentTab == 'step2'">
      <div class="titleBox">
        <div class="mainTitle">General Infomation</div>
        <div class="subTitle">Tell us who you are!</div>
      </div>
      <div class="formBox">
        <form @submit.prevent="validateStep2">
          <div class="form-group formHelfCeil">
          <label class="formLabel">Name</label>
          <input type="text" class="formControl" placeholder="Name" v-model="name">
        </div>
        <div class="form-group formHelfCeil" :class="{'error' : attemptSubmit2 && missingPhone}">
          <label class="formLabel">Phone <span class="required">*</span></label>
          <input type="text" class="formControl" placeholder="Phone" v-model="phone">
          <div class="formFeedback" v-if="attemptSubmit2 && missingPhone">This field is required.</div>
        </div>
        <div class="form-group">
          <label class="formLabel">Birth Date</label>
          <select name="" class="formControl formThreeCeil">
            <option value="">YYYY</option>
          </select>
          <select name="" class="formControl formThreeCeil">
            <option value="">MM</option>
          </select>
          <select name="" class="formControl formThreeCeil">
            <option value="">DD</option>
          </select>
        </div>
        <div class="form-group">
          <label class="formLabel">CVV</label>
          <select name="" class="formControl formHelfCeil">
            <option value="">City</option>
          </select>
          <select name="" class="formControl formHelfCeil">
            <option value="">Dist</option>
          </select>
          <input type="text" class="formControl" placeholder="Address Detail" style="margin-top:10px;" v-model="address">
        </div>
        <button class="formButton" @click="nextStep3">SUBMIT & NEXT</button>
        </form>
      </div>
    </div>
    <!-- step2 -->
    <div class="step3" v-if="currentTab == 'step3'">
      <div class="titleBox">
        <div class="mainTitle">Update Profile Picture</div>
        <div class="subTitle">We wanna know you more!</div>
      </div>
      <div class="formBox">
        <div class="form-group uploadBox">
          <span class="uploadIcon"><i class="far fa-images"></i></span>
          <span class="uploadTxt">
            UPLOAD UP TO 3 PHOTOS<br/>
            MAX SIZE: 150*150px
          </span>
          <input type="file" class="formControl uploadInput">
        </div>
        <button class="formButton" @click="nextStep4">SUBMIT & NEXT</button>
      </div>
    </div>
    <!-- step3 -->
    <div class="step4" v-if="currentTab == 'step4'">
      <div class="titleBox">
        <div class="mainTitle">Payment Method</div>
        <div class="subTitle">Add your credit card infomation! </div>
      </div>
      <div class="formBox">
        <div class="form-group cardNumber">
          <label class="formLabel">Card Number</label>
          <input type="text" class="formControl" placeholder="Card Number">
          <i class="fab fa-cc-visa"></i>
        </div>
        <div class="form-group formHelfCeil">
          <label class="formLabel">Cardholder Name</label>
          <input type="text" class="formControl" placeholder="Cardholder Name">
        </div>
        <div class="form-group formHelfCeil">
          <label class="formLabel">Bank Name</label>
          <input type="text" class="formControl" placeholder="Bank Name">
        </div>
        <div class="form-group formThreeCeil">
          <label class="formLabel">CVV</label>
          <input type="text" class="formControl">
        </div>
        <div class="form-group expireDate">
          <label class="formLabel">Expire Date</label>
          <select name="" class="formControl formHelfCeil">
            <option value="">MM</option>
          </select>
          <select name="" class="formControl formHelfCeil">
            <option value="">DD</option>
          </select>
        </div>
        <button class="formButton" @click="nextFinish">DONE</button>
      </div>
    </div>
    <!-- step4 -->
    <div class="finish" v-if="currentTab == 'finish'">
      <div class="titleBox">
        <div class="mainTitle">Congratulations</div>
        <div class="subTitle">Now you’re one of us!</div>
      </div>
      <div class="formBox">
        <div class="formImg">
          <i class="far fa-smile"></i>
        </div>
        <div class="formTxt">Redirected to homepage after {{time}} seconds…</div>
      </div>
    </div>
    <!-- finish -->
  </div>
</template>

<script>
// import axios from 'axios'
// import { required, sameAs } from 'vuelidate/lib/validators'
// import {FormWizard, TabContent} from 'vue-form-wizard'
// import 'vue-form-wizard/dist/vue-form-wizard.min.css'
export default {
  name: 'Form',
  data () {
    return {
      currentSetTab:['step1','step2','step3','step4'],
      currentTab:'step1',
      attemptSubmit: false,
      attemptSubmit2: false,
      account:'',
      password:'',
      checkPassword:'',
      name:'',
      phone:'',
      // birthYear:'',
      // birthMonth:'',
      // birthDay:'',
      // CVVCity:'',
      // CVVDist:'',
      address:'',
      timeEnd: 5,
      time: 5,
      timeFunc: 0
      
    }
  },
  computed: {
    missingAccount: function () { return this.account === ''; },
    missingPassword: function () { return this.password === ''; },
    missingCheckPassword: function(){ return this.checkPassword === '';},
    errorCheckPassword: function(){ return this.checkPassword != this.password},
    missingPhone: function(){return this.phone === '';},
  },
  methods: {
    validateStep1() {
      this.attemptSubmit = true;
      if (this.missingAccount) event.preventDefault();
      if (this.missingPassword) event.preventDefault();
    },
    validateStep2() {
      this.attemptSubmit2 = true;
      if (this.phone) event.preventDefault();
    },
    nextStep2(){
      if(this.attemptSubmit){
        this.currentTab = 'step2'
        // console.log(this.currentTab)
      }
    },
    nextStep3(){
      if(this.attemptSubmit2){
        this.currentTab = 'step3'
        // console.log('step3')
      }
    },
    nextStep4(){
      this.currentTab = 'step4'
      // console.log('step4')
    },
    nextFinish(){
      this.currentTab = 'finish'
      this.timeFunc = setInterval(this.update, 1000);
    },
    update: function() {
      const now = 0;
      if (now >= this.time) {
          clearInterval(this.timeFunc);
          this.time = 0;
          return;
      }
      this.time = Math.floor(this.timeEnd--);
      if(this.time === 0){
        // this.currentTab = 'step1'
        this.$router.go(this.$router.currentRoute)
        console.log(this.$router)
        
      }
      console.log(this.time)
    }
  }
}
</script>




